body {
  margin: 0;
  font-size: 14px;
  line-height: 1.6;
  font-family: "Montserrat","Helvetica Neue",Arial,sans-serif;
  color: #000;
  font-weight: normal;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white !important;
}

p.loading {
  position: relative;
  top: 100px;
  padding-left: 50px;
}

